.home-nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.card-profit {
  border: none;
  background: linear-gradient(110.74deg, rgba(235, 0, 255, 0.75) -17.43%, rgba(47, 13, 255, 0.75) 108.14%);
  border-radius: 12px;
}
.card-profit__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  color: #FFFFFF;
  font-weight: 600;
  font-size: 20px;
  line-height: 106.8%;
  letter-spacing: 0.015em;
}
.card-profit__body {
  color: #FFFFFF;
  font-weight: 600;
  font-size: 48px;
  line-height: 106.8%;
  letter-spacing: 0.015em;
}
.card-profit__btn {
  color: #FFFFFF;
  border: 2px solid #FFFFFF;
  border-radius: 12px;
  transition: all 0.4s ease-in;
}
.card-profit__btn:hover,
.card-profit__btn:focus,
.card-profit__btn:active {
  color: #FFFFFF;
  background: linear-gradient(110.74deg, rgba(235, 0, 255, 0.75) -17.43%, rgba(47, 13, 255, 0.75) 108.14%);
  border: 2px solid transparent;
}
.text-pinck {
  color: #EB00FF;
}
@media (max-width: 768px) {
  .home-nav {
    flex-direction: column;
    align-items: flex-start;
  }
  .home-nav__btn {
    margin-top: 15px;
  }
}
@media (max-width: 575.98px) {
  .card-profit__body {
    font-size: 40px;
  }
}
@media (max-width: 320.98px) {
  .card-profit__body {
    font-size: 36px;
  }
}
